@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@400;500;600;700&display=swap");
:root {
  --primary-font:"Quicksand", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --dark-bg: #1D2123;
  --grey: #FFFFFF1C;
  --darker-bg: #1A1E1F;
  --yellow: #FACD66;
  --glow-yellow: 0px 0px 11px rgba(250, 205, 102, 0.25);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  list-style: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--primary-font);
  background-color: var(--dark-bg);
}

h1, h2, h3 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

.nav {
  width: 100vw;
}
@media (min-width: 64em) {
  .nav__search {
    position: absolute;
    left: 9.375rem;
    bottom: 1.25rem;
  }
}
.nav .mobile-nav {
  background-color: var(--darker-bg);
  max-width: 340px;
  height: 83vh;
  transform: translateX(-380px);
  transition: all 1s ease-in-out;
}
.nav .mobile-nav li {
  display: flex;
  font-size: 1.15rem;
  font-weight: 500;
  width: 100%;
  align-items: center;
  gap: 25px;
  cursor: pointer;
  opacity: 0.7;
  vertical-align: middle;
  transition: all 300ms ease-in-out;
}
.nav .mobile-nav li:not(:last-child) {
  margin-bottom: 50px;
}
.nav .mobile-nav li svg * {
  transition: all 200ms ease-in-out;
}
.nav .mobile-nav li * {
  align-self: center;
}
.nav .mobile-nav li:hover {
  opacity: 1;
}
.nav .mobile-nav li:hover svg * {
  opacity: 1;
  fill: var(--yellow) !important;
}

.aside__itemsCtn {
  background-color: var(--darker-bg);
}
.aside__itemsCtn li svg {
  cursor: pointer;
}
.aside__itemsCtn li svg * {
  transition: all 200ms ease-in-out;
}
.aside__itemsCtn li svg:hover * {
  opacity: 1;
  fill: var(--yellow) !important;
}